<template>
  <div style="display: flex;flex-wrap: wrap;">
    <div class="forTollBox" v-for="(t,index) in this.webUrl" :key="index">
      <div class="toolBox" @click="page(t[3])">
        <div class="toolTitle">
          <img :src="t[1]">
          <h3>{{ t[0] }}</h3>
        </div>
        <hr>
        <div class="toolBoxH3">{{ t[2] }}</div>
        <h5>{{ t[3] }}</h5>
      </div>
    </div>
  </div>
</template>

<script>
import tooljs from "../js/toolWeb.js"
export default {
  name: "ToolWeb",
  data() {
    return {
      webUrl:[]
    };
  },
  created() {
    this.webUrl=tooljs.webUrl
  },
  methods:{
    page(e){
      window.open(e)
    }
  }
}
</script>


<style scoped>
@import "../less/ToolWeb.less";
</style>